export const ErrorHandler = function() {
  // Any middlewares
  this.middlewares = [];

  // Run handlers
  this.execute = async function(context) {
    let self = this;
    let prevIndex = -1;

    async function running(index) {
      if (index === prevIndex) {
        throw new Error("next() called multiple times");
      }

      prevIndex = index;

      let middleware = self.middlewares[index];
      if (middleware) {
        // params: ctx, next
        await middleware(context, async () => {
          return await running(index + 1);
        });
      }
    }

    await running(0);
  };

  // Push handler
  this.use = function(...middlewares) {
    this.middlewares.push(...middlewares);
  };
};

export default { ErrorHandler };
