import {
  getProducts,
  getProduct,
  createProduct,
  updateProduct
} from "../services/products.service";

export const state = () => ({
  products: [],
  product: null,
  productPaginate: {}
});

export const actions = {
  async fetchProducts({ commit }, params = {}) {
    const { products, paginate } = await getProducts(params);
    commit("updateProducts", products);
    commit("updateProductPaginate", paginate);
  },
  async fetchProduct({ commit }, id) {
    const { product } = await getProduct(id);
    commit("updateProduct", product);
  },
  async fetchCreateProduct(ctx, params) {
    await createProduct(params);
  },
  async fetchUpdateProduct(ctx, { id, form }) {
    await updateProduct(id, form);
  }
};

export const mutations = {
  updateProducts(state, products) {
    state.products = products;
  },
  updateProduct(state, product) {
    state.product = product;
  },
  updateProductPaginate(state, paginate) {
    state.productPaginate = paginate;
  }
};

export const getters = {
  getProducts(state) {
    return state.products;
  },
  getProductPaginate(state) {
    return state.productPaginate;
  },
  getProduct(state) {
    return state.product;
  }
};
