const ACCESS_TOKEN_KEY = "accessToken";
const REFRESH_TOKEN_KEY = "refreshToken";

function getTokensInStorage() {
  function get(name) {
    return localStorage.getItem(name);
  }

  const [accessToken, refreshToken] = [
    get(ACCESS_TOKEN_KEY),
    get(REFRESH_TOKEN_KEY)
  ];

  return {
    accessToken,
    refreshToken
  };
}

function setDataInStorage(key, value) {
  localStorage.setItem(key, value);
}

function destroyDataInStorage(...keys) {
  keys.forEach(key => localStorage.removeItem(key));
}

export function setTokens(access, refresh) {
  setDataInStorage(ACCESS_TOKEN_KEY, access);
  setDataInStorage(REFRESH_TOKEN_KEY, refresh);
}

export function destroyTokens() {
  destroyDataInStorage(ACCESS_TOKEN_KEY, REFRESH_TOKEN_KEY);
}

export const getTokens = function() {
  return getTokensInStorage();
};
