import http from "../plugins/axios";

async function getProducts(params) {
  const response = await http.post("/products", null, { params });
  return response.data;
}

async function getProduct(id) {
  const response = await http.post(`/products/${id}`);
  return response.data;
}

async function createProduct(formParams) {
  const formData = new FormData();
  for (let param in formParams) {
    formData.append(param, formParams[param]);
  }

  await http.post("/products/create", formData);
}

async function updateProduct(id, formParams) {
  const formData = new FormData();
  for (let param in formParams) {
    formData.append(param, formParams[param]);
  }

  await http.post(`/products/${id}/update`, formData);
}

async function deleteProduct() {}

export { getProduct, getProducts, updateProduct, deleteProduct, createProduct };
