import Vue from "vue";
import VueRouter from "vue-router";
import VueRouterMiddleware from "vue-router-middleware-system";
import Home from "../views/Home.vue";

// Middlewares
import CheckAuthMiddleware from "./middlewares/check-auth.middleware";
import LoggedUserMiddleware from "./middlewares/logged-user.middleware";

Vue.use(VueRouter);

const routes = [
  {
    path: "/login",
    name: "Login",
    component: () => import("../views/Login.vue"),
    meta: { middleware: [LoggedUserMiddleware] }
  },
  {
    path: "/",
    name: "Home",
    component: Home,
    meta: { middleware: [CheckAuthMiddleware] }
  },
  {
    path: "/users",
    name: "Users",
    component: () => import("../views/users/Users"),
    meta: { middleware: [CheckAuthMiddleware] }
  },
  {
    path: "/products",
    name: "Products",
    component: () => import("../views/products/Products"),
    meta: { middleware: [CheckAuthMiddleware] }
  },
  {
    path: "/orders",
    name: "Orders",
    component: () => import("../views/orders/Orders"),
    meta: { middleware: [CheckAuthMiddleware] }
  },
  {
    path: "/order-statuses",
    name: "OrderStatuses",
    component: () => import("../views/orders/OrderStatuses"),
    meta: { middleware: [CheckAuthMiddleware] }
  },
  {
    path: "/categories",
    name: "Categories",
    component: () => import("../views/categories/Categories"),
    meta: { middleware: [CheckAuthMiddleware] }
  },
  {
    path: "/about",
    name: "About",
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () =>
      import(/* webpackChunkName: "about" */ "../views/About.vue")
  }
];

const router = new VueRouter({
  routes,
  mode: "history"
});

router.beforeEach(VueRouterMiddleware());

export default router;
