function getNamesDialog(name) {
  const mask = /^[a-zA-Z]+:[a-zA-Z]+$/;
  if (!mask.test(name)) {
    throw new Error("Incorrect mask.");
  }

  return name.split(":");
}

export const state = () => ({
  dialogs: {
    category: {
      update: false,
      create: false
    },
    status: {
      update: false,
      create: false
    }
  }
});

export const actions = {
  show({ commit }, name) {
    commit("showStateDialog", getNamesDialog(name));
  },
  close({ commit }, name) {
    commit("closeStateDialog", getNamesDialog(name));
  }
};

export const mutations = {
  showStateDialog(state, [dialog, name]) {
    state.dialogs[dialog][name] = !state.dialogs[dialog][name];
  }
};
