import Vue from "../../main";
import ErrorTypes from "../error-types";

export default function ErrorToast(error, next) {
  const response = error.response;
  const data = response ? response.data : {};
  const errorType = data.error ? data.error : null;
  console.log(error.response);

  const findError = errorType
    ? ErrorTypes.find(e => e.type === errorType)
    : null;

  const config = {
    color: "red",
    x: "right",
    y: "top"
  };

  if (findError) {
    Vue.$toast(findError.msg, config);
  } else {
    Vue.$toast("Системная ошибка. Обратитесь к администратору.", config);
  }

  next();
}
