<template>
  <v-app-bar
    v-if="auth"
    class="v-app-bar--fixed elevation-4"
    color="white lighten-4"
    flat
    dense
  >
    <v-app-bar-nav-icon></v-app-bar-nav-icon>

    <v-toolbar-title>
      <router-link
        :to="{ name: 'Home' }"
        class="font-weight-bold text--secondary text-decoration-none"
      >
        Глазурь | Панель управления
      </router-link>
    </v-toolbar-title>

    <v-spacer></v-spacer>

    <v-btn icon>
      <v-icon>mdi-magnify</v-icon>
    </v-btn>

    <v-btn icon>
      <v-icon>mdi-bell</v-icon>
    </v-btn>

    <v-menu>
      <template v-slot:activator="{ on: menu, attrs }">
        <v-btn icon v-bind="attrs" v-on="{ ...menu }" class="my-5">
          <v-icon>mdi-dots-vertical</v-icon>
        </v-btn>
      </template>
      <v-list>
        <v-list-item @click.prevent="logout" link>
          <v-list-item-icon class="mx-1">
            <v-icon>mdi-account-multiple</v-icon>
          </v-list-item-icon>
          <v-list-item-title>
            Выход
          </v-list-item-title>
        </v-list-item>
      </v-list>
    </v-menu>
  </v-app-bar>
</template>

<script>
import { mapGetters, mapActions } from "vuex";

export default {
  name: "Navbar",
  computed: {
    ...mapGetters({
      auth: "getAuth"
    })
  },
  methods: {
    ...mapActions({
      exit: "fetchLogout"
    }),
    async logout() {
      await this.exit();
      await this.$router.push({ name: "Login" });
    }
  }
};
</script>

<style scoped></style>
