import http from "../plugins/axios";

export const getOrderStatuses = async function() {
  const response = await http.post("/order-statuses");
  return response.data;
};

export const getOrderStatus = async function(id) {
  const response = await http.post("/order-statuses/" + id);
  return response.data;
};

export const createOrderStatus = async function(params) {
  const response = await http.post("/order-statuses/create", params);
  return response.data;
};

export const updateOrderStatus = async function(id, body) {
  const response = await http.post("/order-statuses/" + id + "/edit", body);

  return response.data;
};
