import http from "../plugins/axios";

async function getCategories(params) {
  const response = await http.post("/categories", null, { params });
  return response.data;
}

async function getCategory(id) {
  const response = await http.post(`/categories/${id}`);
  return response.data;
}

async function createCategories(data) {
  await http.post("/categories/create", data);
}

async function updateCategory(id, data) {
  await http.post(`/categories/${id}/update`, data);
}

export { getCategories, getCategory, createCategories, updateCategory };
