import {
  getOrderStatuses,
  getOrderStatus,
  createOrderStatus,
  updateOrderStatus
} from "../services/orders-status.service";

export const state = () => ({
  statuses: [],
  status: null
});

export const actions = {
  async fetchOrderStatuses({ commit }) {
    const { statuses } = await getOrderStatuses();
    commit("updateOrderStatuses", statuses);
  },
  async fetchOrderStatus({ commit }, id) {
    const { status } = await getOrderStatus(id);
    commit("updateOrderStatus", status);
  },
  // eslint-disable-next-line no-unused-vars
  async fetchCreateOrderStatus(ctx, params) {
    await createOrderStatus(params);
  },
  async fetchUpdateOrderStatus(ctx, { id, body }) {
    await updateOrderStatus(id, body);
  }
};

export const mutations = {
  updateOrderStatuses(state, statuses) {
    state.statuses = statuses;
  },
  updateOrderStatus(state, status) {
    state.status = status;
  }
};

export const getters = {
  getOrderStatuses(state) {
    return state.statuses;
  },
  getOrderStatus(state) {
    return state.status;
  }
};
