export const state = () => ({
  error: null
});

export const actions = {
  pushError({ commit }, { error, config = {} }) {
    commit("updateError", { error, config });
  }
};

export const mutations = {
  updateError(state, error) {
    state.error = error;
  }
};

export const getters = {
  getError(state) {
    return state.error;
  }
}