import { ErrorHandler } from "./handler";
import ErrorToastMiddleware from "./middlewares/error-toast.middleware";
import SuccessToast from "./middlewares/success-toast.middleware";
import UnauthorizedMiddleware from "./middlewares/unauthorized.middleware";

const handler = new ErrorHandler();

handler.use(ErrorToastMiddleware);
handler.use(UnauthorizedMiddleware);
handler.use(SuccessToast);

export default handler;
