import http from "../plugins/axios";
import {
  setTokens,
  getTokens,
  destroyTokens
} from "../services/tokens.service";

export const state = () => ({
  auth: false,
  client: null
});

export const actions = {
  async fetchLogin({ commit }, { login, password }) {
    const response = await http.post("/auth/login", { login, password });
    const { tokens, worker } = response.data;

    setTokens(...Object.keys(tokens).map(token => tokens[token]));

    commit("updateAuthState", true);
    commit("updateClientState", worker.user);
  },
  async fetchClientInfo({ commit }) {
    const { accessToken } = getTokens();
    if (!accessToken) {
      return;
    }

    const response = await http.post("/auth/client-info");
    const { worker } = response.data;
    commit("updateAuthState", true);
    commit("updateClientState", worker.user);
  },
  async fetchLogout({ commit }) {
    const { refreshToken } = getTokens();
    await http.post("/auth/logout", { refresh: refreshToken });

    commit("updateClientState", null);
    commit("updateAuthState", false);

    destroyTokens();
  }
};

export const mutations = {
  updateAuthState(state, data) {
    state.auth = data;
  },
  updateClientState(state, data) {
    state.client = data;
  }
};

export const getters = {
  getAuth(state) {
    return state.auth;
  },
  getClient(state) {
    return state.client;
  }
};
